.with-products{
	padding-top: 20px;
	padding-bottom: 18px;
	@media (min-width:992px) {
		
		padding-bottom: 50px;
	}

}

.sale .section-title span{
	width: 20px;
	height: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	background: #000;
	font-style: normal;
	font-weight: 400;
	font-size: 10px;
	line-height: 14px;
	letter-spacing: 0.03em;
	text-transform: lowercase;
	color: #F8F8F8;
	margin-right: 10px;
	@media (min-width:992px) {
	
		width: 30px;
		height: 30px;
	}
}