.clear-cart {
	font-weight: 400;
	font-size: 8px;
	line-height: 11px;
	/* identical to box height */

	letter-spacing: 0.05em;
	text-transform: lowercase;
	cursor: pointer;
	color: #c9c9c9;
	margin-bottom: 24px;
	@media (min-width: 992px) {
		margin-bottom: 0;
		font-size: 16px;
		line-height: 22px;
	}
	.svg {
		width: 20px;
		height: 20px;
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		margin-right: 5px;
		@media (min-width: 992px) {
			margin-right: 14px;
			width: 24px;
			height: 24px;
		}
	}
	&:hover {
		text-decoration: none;
		color: #c9c9c9;
	}
}
.cart-page {
	.page-title {
		margin-bottom: 23px;
		@media (min-width: 992px) {
			margin-bottom: 40px;
		}
	}
}

.cart {
	&__item {
		margin-bottom: 15px;
	}

	.item {
		&__img {
			width: 50%;
			height: 138px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__summ {
			padding-left: 10px;
		}
		&__top {
			margin-bottom: 21px;
		}
		&__middle {
			margin-bottom: 40px;
			.item__name {
				font-weight: 600;
				font-size: 12px;
				line-height: 16px;
				letter-spacing: 0.05em;

				.name {
					color: #34353a;
					margin-bottom: 8px;
				}
				.opc {
					font-weight: 400;
					font-size: 12px;
					line-height: 16px;
					color: #90939c;
				}
			}
			.item__remove {
				font-weight: 400;
				font-size: 8px;
				line-height: 11px;
				letter-spacing: 0.05em;
				text-transform: lowercase;
				color: #c9c9c9;
				padding-left: 10px;
				.svg {
					width: 20px;
					height: 20px;
					background-size: contain;
					background-repeat: no-repeat;
					background-position: center;
					margin-right: 5px;
				}
			}
		}
		&__bottom {
			.item__size {
				.size-title {
					font-weight: 600;
					font-size: 12px;
					line-height: 16px;
					letter-spacing: 0.05em;
					color: #34353a;
					margin-bottom: 8px;
				}
				.size-value {
					font-weight: 400;
					font-size: 12px;
					line-height: 16px;
					letter-spacing: 0.05em;
					text-transform: lowercase;

					color: #000000;
				}
			}
		}
	}
	.summ {
		&__title {
			font-weight: 400;
			font-size: 12px;
			line-height: 16px;
			letter-spacing: 0.05em;

			color: #000000;
		}
		&__value {
			font-weight: 600;
			font-size: 16px;
			line-height: 22px;
			/* identical to box height */

			letter-spacing: 0.05em;
			text-transform: lowercase;

			color: #000000;
		}
	}
}

.cart-desktop {
	.table {
		thead {
			border-top: 1px solid #c9c9c9;
			border-bottom: 1px solid #c9c9c9;
			th {
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
				/* identical to box height */

				letter-spacing: 0.05em;
				text-transform: lowercase;

				color: #000000;
			}
		}
		tbody {
		}
	}
	.cart-item {
		&__image {
			width: 206px;
			height: 170px;
			margin-right: 70px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__name {
			padding-top: 10px;
			font-weight: 600;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: #34353a;
			margin-bottom: 10px;
		}
		&__opc {
			font-size: 16px;
			line-height: 22px;
			/* identical to box height */

			letter-spacing: 0.05em;
			text-transform: lowercase;

			color: #90939c;
		}
		&__size {
			padding-top: 10px;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			/* identical to box height */

			letter-spacing: 0.05em;
			text-transform: lowercase;

			color: #000000;
		}
		&__price {
			padding-top: 10px;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			/* identical to box height */

			letter-spacing: 0.05em;
			text-transform: lowercase;

			color: #000000;
		}
		&__summ {
			padding-top: 10px;

			.value {
				font-weight: 600;
				font-size: 16px;
				line-height: 22px;
				/* identical to box height */

				letter-spacing: 0.05em;
				text-transform: lowercase;

				color: #000000;
				margin-bottom: 50px;
			}
		}
		&__remove {
			.svg {
				display: block;
				width: 24px;
				height: 24px;
				background-size: contain;
				background-repeat: no-repeat;
				background-position: center;
				margin-right: 5px;
			}
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			/* identical to box height */

			letter-spacing: 0.05em;
			text-transform: lowercase;

			color: #c9c9c9;
		}
	}
}
.svg {
	&.--svg__reload {
		display: block;
		width: 14px;
		height: 13px;
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		margin-right: 15px;
		@media (min-width: 992px) {
			width: 20px;
			height: 18px;
			margin-right: 20px;
		}
	}
}
.cart-info {
	margin-bottom: 64px;
	@media (min-width: 992px) {
		margin-bottom: 80px;
	}
	hr{
		margin-top: 0;
		margin-bottom: 20px;
	}
	&__buttons{
		margin-bottom: 43px;
		@media (min-width: 992px) {
			margin-bottom: 0;
			position: relative;
	
		}
	}
	.btn {
		height: 44px;
		@media (min-width: 992px) {
			height: 50px;
			position: sticky;
			top: 15px;
		}
		&.reload {
			width: 189px;
			margin-bottom: 14px;
			@media (min-width: 992px) {
				margin-bottom: 0;
				margin-right: 20px;
				width: 231px;
			}
		}
		&.continue {
			width: 252px;
			@media (min-width: 992px) {
				width: 259px;
			}
		}
	}

	.info__table{
		margin-bottom: 20px;
		@media (min-width: 992px) {
			max-width: 430px;
			margin-bottom: 40px;
		}
		
		.table__item{
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
			line-height: 16px;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			margin-bottom: 25px;
			@media (min-width: 992px) {
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
				margin-bottom: 20px;

			}
			.title{
				color: #000000;
				padding-right: 10px;
				text-align: left;
				display: flex;
				flex-grow: 1;
				&::after{
					content: '';
					transform: translateY(-25%);
					border-bottom: 1px dashed #737780;
					height: 1em;
					margin: 0 .4em;
					display: block;
					flex-grow: 1;
				}
			}
			.value{
				color: #90939C;
				padding-left: 10px;
				text-align: right;
				@media (min-width: 992px) {
					text-align: left;
				}
			}
		}
	}
	&__checkout{
		margin-bottom: 30px;
		@media (min-width: 992px) {
			
			margin-bottom: 20px;
		}
		p{
			font-weight: 400;
			font-size: 15px;
			line-height: 20px;
			/* identical to box height */

			letter-spacing: 0.05em;

			/* Н3 */
			margin-bottom: 10px;
			color: #B0B3B8;
			@media (min-width: 992px) {
				font-size: 16px;
				line-height: 22px;
			}
		}
	}
	&__checkout-btns{
		.btn{
			width: 166px;
			height: 44px;
			&:first-child{
				margin-right: 10px;
			}
			&:last-child{
				margin-left: 10px;
			}
			@media (min-width: 992px) {
				width: 216px;
				height: 50px;
				
			}
		}
		
	}
}
.text-danger{
	color: $gold!important;
}