.footer {
	&__wrapper {
		padding-top: 20px;
		padding-bottom: 20px;
		border-top: 1px solid #c1c1c1;
		border-bottom: 1px solid #c1c1c1;
	}
	&__column {
		margin-bottom: 40px;
		@media (min-width: 992px) {
			margin-right: 64px;
			margin-bottom: 0;
		}
		.column__title {
			color: var(--Orange, #b89a8a);
			font-family: $montserat;
			font-weight: 600;
			font-size: 16px;
			line-height: 22px;
			margin-bottom: 10px;
			text-align: center;
			@media (min-width: 992px) {
				text-align: left;
			}
		}
		ul {
			padding-left: 0;
			li {
				list-style-type: none;
				text-align: center;
				@media (min-width: 992px) {
					text-align: left;
				}
				&:not(:last-child) {
					margin-bottom: 10px;
				}
				a {
					font-family: "Open Sans", sans-serif;
					font-style: normal;
					font-weight: 400;
					font-size: 14px;
					line-height: normal;
					letter-spacing: 0.7px;

					color: #737780;
					position: relative;
					&::after {
						content: "";
						position: absolute;
						top: 100%;
						left: 0;
						height: 1px;
						background: #737780;
						width: 100%;
						max-width: 0px;
						transition: max-width 0.3s;
					}
					&:hover {
						&::after {
							max-width: 100%;
						}
					}
				}
			}
		}
	}
	&__main {
		padding-top: 21px;
		padding-bottom: 21px;
		border-top: 1px solid #c1c1c1;
		border-bottom: 1px solid #c1c1c1;
		@media (min-width: 992px) {
			border: none;
			padding: 0;
		}
		.logo {
			width: 163px;
			margin-bottom: 35px;
			@media (min-width: 992px) {
				width: 148px;
				margin-bottom: 20px;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
	&__contacts {
		font-family: $montserat;
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		letter-spacing: 0.05em;

		color: #737780;
		span {
			color: $gold;
			@media (min-width: 992px) {
				margin-right: 10px;
			}
		}
		a {
			font-family: $montserat;
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: #737780;
			position: relative;
			&::after {
				content: "";
				position: absolute;
				top: 100%;
				left: 0;
				height: 1px;
				background: #737780;
				width: 100%;
				max-width: 0px;
				transition: max-width 0.3s;
			}
			&:hover {
				&::after {
					max-width: 100%;
				}
			}
			span {
				color: $black;
			}
		}
		.contact {
			justify-content: center;
			&:not(:last-child) {
				margin-bottom: 15px;
			}
			@media (min-width: 992px) {
				justify-content: flex-start;
			}
		}
		.phones {
			span {
				margin-bottom: 10px;
				@media (min-width: 992px) {
					margin-bottom: 0;
					margin-right: 10px;
				}
			}
		}
	}
	.copyright {
		font-family: "Open Sans";
		font-style: normal;
		font-weight: 400;
		font-size: 10px;
		line-height: 14px;
		/* identical to box height */

		letter-spacing: 0.05em;
		text-transform: lowercase;

		/* gray */
		padding-top: 10px;
		padding-bottom: 30px;
		color: #737780;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
			padding-top: 30px;
		}
	}
}
