.error-404{
	background: #FFFFFF;
padding-top: 30px;
padding-bottom: 15px;
margin-bottom: 60px;
@media (min-width: 992px) {
	padding-top: 120px;
	padding-bottom: 50px;
	margin-bottom: 80px;
}
	.svg{
		width: 202px;
		height: 100px;
		display: block;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			margin-bottom: 0;
			padding-right: 31px;
			width: 405px;
			height: 272px;
		}
	}
	&__text{
		
		@media (min-width: 992px) {
			padding-left: 31px;
		}
	}
	&__title{
		font-weight: 300;
		font-size: 20px;
		line-height: 27px;
		letter-spacing: 0.12em;
		text-transform: uppercase;
		color: $gold;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			font-size: 36px;
			line-height: 49px;
			margin-bottom: 40px;
		}
	}
	&__comment{
		font-weight: 300;
		font-size: 16px;
		line-height: 22px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: lowercase;
		margin-bottom: 40px;
		/* black */

		color: #34353A;
		@media (min-width: 992px) {
			text-align: left;
			font-size: 16px;
			line-height: 22px;
		}

	}
	&__wrapper{
		margin-bottom: 40px;
	}
}