.card{
	border-radius: 0;
	border:none;
	padding: 5px;
	background: #FFFFFF;
	transition: box-shadow 0.3s;
	position: relative;
	z-index: 1;
	border: 1px solid #CFCFCF;
	&__image{
		width: 100%;
		height: 130px;
		margin-bottom: 10px;
		overflow: hidden;
		@media (min-width:992px) {
			height: 220px;
		}
		img{
			object-fit: cover;
			height: 100%;
			border-radius: 0;
			max-width: 100%;
		}
	}
	.card-title{
		color:  #3F3F3F;
		font-family: $montserat;
		font-size: 10px;
		font-style: normal;
		font-weight: 500;
		line-height: normal;
		margin-bottom: 10px;
		@media (min-width:992px) {
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 20px;
		}
	}
	.card-body{
		padding: 0;
	}
	.card-price{
		font-style: normal;
		font-weight: 600;
		font-size: 9.56522px;
		line-height: 13px;
		/* identical to box height */
		letter-spacing: 0.05em;
		text-transform: lowercase;
		color: #90939C;
		@media (min-width:992px) {
			font-size: 16px;
			line-height: 22px;
		}
		margin-bottom: 10px;
	}
	&__hidden{
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 5;
		background: #fff;
		width: 100%;
		opacity: 0;
		visibility: hidden;
		padding-right: 5px;
		padding-left: 5px;
		padding-bottom: 5px;
		transition: opacity 0.3s;
		.prices{
			margin-bottom: 8px;
			&__item{
				&:not(:last-child){
					margin-right: 15px;
				}
			
			}
			&__title{
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
					line-height: 22px;
				font-size: 13px;
				line-height: 18px;
				letter-spacing: 0.05em;
				text-transform: lowercase;
				color: #737780;
				@media (min-width:992px) {
					font-size: 16px;
					line-height: 22px;
				}

			}
			&__value{
				font-style: normal;
				font-weight: 600;
				font-size: 13px;
				line-height: 18px;
				/* identical to box height */

				letter-spacing: 0.05em;
				text-transform: lowercase;

				color: #90939C;
				@media (min-width:992px) {
					font-size: 16px;
					line-height: 22px;
				}

			}
		}
		.sizes{
			&__item{
				border: 1px solid #C1C1C1;
				padding: 5px;
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
				/* identical to box height */

				letter-spacing: 0.05em;
				text-transform: lowercase;

				/* black */

				color: #34353A;
				&:not(:last-child){
					margin-right: 5px;
				}
			}
		}
		.buttons{
			margin-top: 11px;
			button{
				&:not(:last-child){
					margin-right: 5px;
				}
			}
			.wish{
				width: 54px;
				.svg{
					width: 20px;
					height: 100%;
					background-size: contain;
					background-repeat: no-repeat;
					background-position-y: center;
				}
				&.active{
					background: #45474e;
				}
			}
		}
	}
	&:hover{
		z-index: 6;
		box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
		.card__hidden{
			opacity: 1;
			visibility: visible;
			box-shadow: 0px 4px 5px 0px  rgba(0, 0, 0, 0.25);
		}
	}
	.sticker{
		display: none!important;
		position: absolute;
		top: 15px;
		left: 5px;
		width: 30px;
		height: 11px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-style: normal;
		font-weight: 400;
		font-size: 7px;
		line-height: 10px;
		/* identical to box height */

		letter-spacing: 0.05em;
		text-transform: lowercase;

		/* begue-light */

		color: #F8F8F8;
		@media (min-width:992px) {
			width: 53px;
			height: 20px;
			font-size: 13px;
			line-height: 18px;
		}

	}
}

