html {
	height: 100%;
}
body {
	display: flex;
	flex-direction: column;
	height: 100%;
	font-family: "Open-sans", sans-serif;
	max-width: 100%;
	font-weight: 400;
	min-width: 320px;
	&.lock {
		overflow-y: hidden;
	}
}

header {
	flex: 0 0 auto;
}
main {
	flex: 1 0 auto;
	padding-top: 67px;
	@media (min-width: 992px) {
		padding-top: 0;
	}
}
.container {
	max-width: 1230px;
}
footer {
	flex: 0 0 auto;
}
a {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
.form-control:focus {
	box-shadow: none;
	border-color: #ced4da;
}
.underline {
	text-decoration: none;
	position: relative;
	&::before {
		content: "";
		position: absolute;
		bottom: 4px;
		height: 1px;
		background: $black;
		left: 0;
		width: 100%;
	}
}

.text-dark {
	color: $black;
}

.btn {
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	letter-spacing: 0.05em;
	text-transform: lowercase;
	border-radius: 0;
}
.btn-primary {
	color: #f8f8f8;
	background-color: #b3b7c3;
	height: 48px;
	border-color: #b3b7c3;
	transition: background 0.3;
	&:hover,
	&:active,
	&:focus {
		background-color: #9ea2b1;
		border-color: #9ea2b1;
	}
}
.btn-outline-secondary {
	border-color: #b3b7c3;
}
.header {
	// position: sticky;
	// top:0;
	z-index: 6;
	background: #fff;
	@media (min-width: 992px) {
		position: relative;
	}
	&__phone {
		color: $gold;
		font-weight: 600;
	}
	&__mail {
		span {
			color: $grey;
			font-weight: 400;
		}
		color: $gold;
		font-weight: 600;
	}
	&__icon {
		background-color: $gold;
		width: 30px;
		height: 30px;
		border-radius: 2px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 10px;
		span {
			background-size: cover;
			background-repeat: no-repeat;
			width: 20px;
			height: 20px;
		}
	}
	&__main {
		padding-top: 19px;
		padding-bottom: 16px;
	}
	.main {
		border-bottom: 1px solid #c1c1c1;
		&__logo {
			width: 222px;
			margin-right: 50px;
		}

		.search {
			flex-grow: 1;
			margin-right: 50px;
			input {
				border-radius: 1px;
				padding-left: 3px;
				padding-left: 10px;
				border-right: none;
			}
			.input-group-text {
				border-radius: 1px;
				width: 40px;
				padding: 0;
				background-color: $gold;
				span {
					width: 100%;
					height: 100%;
					background-position: cover;
					background-repeat: no-repeat;
					background-position: center;
				}
			}
		}
		.header__icon {
			span {
				width: 15px;
				height: 18px;
			}
		}
	}
	.lk {
		margin-right: 32px;
		&__title {
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: $black;
		}
		&__comment {
			font-style: normal;
			font-weight: 400;
			font-size: 10px;
			line-height: 14px;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: #737780;
		}
	}
	&__menu {
		padding-top: 11px;
		padding-bottom: 9px;
		a {
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.05em;
			text-transform: capitalize;
			color: $black;
			position: relative;

			&:hover {
				color: #b3b7c3;
			}
		}
		.menu-opener {
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.05em;
			text-transform: capitalize;
			color: $black;
			cursor: pointer;
		}
		.catalog {
			span {
				display: block;
				width: 20px;
				height: 20px;
				background-repeat: no-repeat;
				background-size: cover;
				margin-right: 12px;
			}
		}
	}

	.city {
		button {
			background: none;
			color: $black;
			border: none;
			&::after {
			}
			margin-right: 58px;
		}
		.dropdown-menu {
			max-height: 200px;
			overflow-y: auto;
		}
	}
	&__mobile {
		position: fixed;
		width: 100%;
		background: #fff;
		top: 0;
		padding-top: 10px;
		padding-bottom: 10px;
		border-bottom: 1px solid #c1c1c1;
		.icons {
			a {
				&:not(:last-child) {
					margin-right: 15px;
				}
			}
		}
		.logo {
			width: 73px;
			margin-right: 13px;
		}
		.city {
			margin-right: 40px;
		}
		.header__icon {
			span {
				width: 12px;
				height: 14px;
			}
		}
		.menu {
			display: block;
			width: 34px;
			height: 34px;
			span {
				display: block;
				width: 100%;
				height: 100%;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center;
			}
		}
		.search-icon {
			display: block;
			width: 24px;
			height: 24px;
			span {
				display: block;
				width: 100%;
				height: 100%;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center;
			}
		}
		.city {
			margin-right: 5px;
			button {
				margin-right: 0;
			}
		}
	}
	.menu-opener {
		margin-top: -11px;
		margin-bottom: -9px;
		height: 50px;
		.catalog-menu {
			display: none;
			flex-direction: column;
			top: 50px;
			width: 338px;
			padding: 20px;
			border-radius: 2px;
			border: 1px solid var(--Light-grey, #cfcfcf);
			background: var(--White, #fff);
			left: 0;
			z-index: 2;

			ul {
				padding-left: 0;
				list-style-type: none;

				li {
					&:not(:last-child) {
						margin-bottom: 10px;
					}
					a {
						font-family: $montserat;
						font-style: normal;
						font-weight: 500;
						font-size: 16px;
						line-height: 22px;
						color: $gold;
						position: relative;
						display: flex;
						align-items: center;
						justify-content: space-between;
						padding: 10px 20px;
						border: 1px solid var(--Light-grey, #cfcfcf);
						.svg {
							width: 40px;
							height: 40px;
							&.normal {
								display: block;
							}
							&.hover {
								display: none;
							}
						}
						&:hover {
							color: #fff;
							background: $gold;
							.svg {
								&.normal {
									display: none;
								}
								&.hover {
									display: block;
								}
							}
						}
						&.current {
							color: #fff;
							background: $gold;
						}
					}
				}
			}
			background: #fff;
		}
		&:hover {
			.catalog-menu {
				display: flex;
			}
		}
	}
}
.text-grey {
	color: $grey;
}
.header__top {
	padding-top: 11px;
	padding-bottom: 11px;
	border-bottom: 1px solid #c1c1c1;
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	/* identical to box height */

	letter-spacing: 0.05em;
	.header__icon {
		width: 24px;
		height: 24px;
		span {
			width: 13px;
			height: 13px;
		}
	}
	a {
		padding-right: 5px;
		padding-left: 5px;
	}
}

.back_frame {
	position: fixed;
	width: 100%;
	max-width: 0px;
	left: 0;
	top: 0;
	height: 100%;
	background: $gold;
	opacity: 0.5;
	z-index: 10;
	&.active {
		max-width: 100%;
	}
}

.mobile-menu {
	top: 0;
	right: 0;
	width: 263px;
	max-width: 0;
	overflow-x: hidden;
	height: 100%;
	background: #fff;
	transition: max-width 0.3s;
	z-index: 11;
	&__body{
		height: 100%;
		display: flex;
		flex-direction: column;
	}
	&.active {
		max-width: 320px;
	}
	&__wrapper {
		height: 100%;
		width: 100%;
		padding-top: 70px;
		padding-left: 20px;
		padding-right: 10px;
		padding-bottom: 20px;
		overflow-y: auto;
		.menu-list {
			&__title {
				color: $gold;
				font-family: $montserat;
				font-size: 16px;
				font-style: normal;
				font-weight: 600;
				line-height: normal;
				letter-spacing: 1.92px;
				text-transform: uppercase;
			}
			a{
				
				color: $gold;
				font-family: $montserat;
				font-size: 16px;
				font-style: normal;
				font-weight: 600;
				line-height: normal;
				letter-spacing: 1.92px;
				text-transform: uppercase;
				padding-bottom: 20px;
				margin-bottom: 0;
				&:first-child{
					padding-top: 20px;
				}
				&:last-child{
					padding-bottom: 0;
				}
			}
			&.catalog {
				a {
					color: var(--Light-grey-2, #949494);
					font-family: $montserat;
					font-size: 14px;
					font-style: normal;
					font-weight: 500;
					line-height: normal;
					letter-spacing: 1.68px;
					text-transform: uppercase;
					padding-top: 10px;
					padding-bottom: 10px;
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 0;
					.svg{
						width: 24px;
						height: 24px;
					}
					&:hover {
						text-decoration: underline;
					}
					&.current {
						color: #b3b7c3;
					}
			
				}
			}
			&:not(:first-child) {
				padding-top: 17px;
			}
			&.lk {
				.title {
					font-family: "Open Sans";
					font-style: normal;
					font-weight: 600;
					font-size: 16px;
					line-height: 22px;
					/* identical to box height */

					text-transform: uppercase;

					color: #000000;
					margin-bottom: 20px;
					margin-top: 10px;
				}
				a {
					font-family: "Open Sans";
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 22px;
					letter-spacing: 0.05em;
					text-transform: lowercase;
					color: #737780;
			
				}
			}
		}
	}
	.btn-close {
		position: absolute;
		top: 15px;
		right: 10px;
		background: url(/img/icons/cross.svg?sanitize=true);
		background-repeat: no-repeat;
		background-position: center;

		width: 34px;
		height: 34px;
	}
}
.btn-close {
	background: url(/img/icons/cross.svg?sanitize=true);
	background-repeat: no-repeat;
	background-position: center;
}
