*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}
.btn-primary {
	border-radius: 2px;
	background: #b89a8a;
	color: #fff;
	font-family: "Montserrat", sans-serif;
	font-size: 12px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	border: none;

	@media (min-width: 992px) {
		font-size: 14px;
	}
}
.btn-secondary{
	background: var(--Light-grey, #CFCFCF);
	border: none;
	border-radius: 2px;
}
.form-check {
	input[type="checkbox"],  input[type="radio"]{
		border: none;
		width: 20px;
		height: 20px;
		background: transparent;
		&::before {
			content: "";
			display: block;
			width: 20px;
			height: 20px;
			border-radius: 2px;
			background: #d9d9d9;
		}
		&:focus{
			box-shadow: none;
		}
		&:checked{
			box-shadow: none;
			background-image: none;
			&::before {
				background: url(/img/icons/check-checked.svg?sanitize=true);
			}
		}
	}
	& input[type="radio"]{
		padding-left: 20px;
		margin-top: 0;
		&~label{
			padding-left: 25px;
		}
	}
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-left: 0;

}
