.pagination{
	padding-top: 20px;
	padding-bottom: 60px;
	@media (min-width:768) {
		padding-bottom: 80px;
	}
	border: none;
	.svg{
		width: 4px;
		height: 8px;
	}
	.page-item{
		&:not(:last-child){
			margin-right: 5px;
		}
	}
	.page-link{
		height: 30px;
		width: 26px;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #CFCFCf;
		color: #fff;
		&.current{
			background-color: $gold;
			color: #fff;
		}
		&.side{
			width: 16px;
			border-radius: 0;
		}
	}
}