.left-menu {
	width: 100%;
	margin-bottom: 40px;
	@media (min-width: 992px) {
		margin-bottom: 0;
	}

	ul {
		list-style-type: none;
		padding-left: 0;
	}
	.subcategory {
		max-height: 0px;
		overflow: hidden;
		transition: max-height 0.3s;
		@media (min-width: 992px) {
			margin-bottom: 40px;
			max-height: none;
		}

		li {
			color: $grey;
			font-family: $montserat;
			font-size: 16px;
			font-style: normal;
			font-weight: 400;
			line-height: normal;
			letter-spacing: 0.8px;
			text-transform: capitalize;
			padding-right: 10px;
			padding-left: 10px;
			padding-top: 10px;
			padding-bottom: 10px;
			@media (min-width: 992px) {
				color: #949494;
				font-size: 16px;
			}
		}
	}
	&__item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-family: $montserat;
		font-size: 15px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		letter-spacing: 0.75px;
		height: 44px;
		background-color: $gold;
		padding-right: 10px;
		padding-left: 10px;

		color: #fff;

		@media (min-width: 992px) {
			padding-top: 0;
			padding-bottom: 0;
			margin-bottom: 20px;
			color: $grey;
			font-size: 16px;
			font-style: normal;
			font-weight: 500;
			line-height: normal;
			letter-spacing: 0.8px;
			background-color: transparent;
			height: auto;

			color: $grey;
		}
		.sub-toogle {
			display: block;
			width: 24px;
			height: 24px;
			background-image: url(/img/icons/arrow-down.svg?sanitize=true);
			transition: transform 0.3s;
		}
		&.active {
			.sub-toogle {
				transform: rotate(180deg);
			}
			& ~ .subcategory {
				max-height: 10000px;
			}
		}
	}
}
