.ourproduction {
	padding-top: 20px;
	padding-bottom: 20px;
	@media (min-width: 992px) {
		padding-bottom: 80px;
	}
	&__left {
		color: #060606;

		font-family: $montserat;
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
	}
	&__right {
		overflow: hidden;
		img {
			object-fit: cover;
		}
	}
}
