.cabinet {
	width: 584px;
	max-width: 100%;
	margin: 0 auto;
	margin-bottom: 64px;
	@media (min-width: 992px) {
		margin-bottom: 80px;
	}
	a {
		font-weight: 400;
		font-size: 14px;
		line-height: 19px;
		/* identical to box height */

		letter-spacing: 0.05em;
		text-transform: lowercase;

		color: #000000;
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: 100%;
			left: 0;
			height: 1px;
			background: #000;
			width: 100%;
			max-width: 0px;
			transition: max-width 0.3s;
		}
		&:hover {
			&::after {
				max-width: 100%;
			}
		}
	}
	&__links {
		margin-bottom: 40px;
		a {
			font-style: normal;
			font-weight: 300;
			font-size: 20px;
			line-height: 27px;
			letter-spacing: 0.12em;
			text-transform: uppercase;
			color: #CFCFCF;
			font-family: $montserat;

			&.current {
				color: $gold;
			}
			&::after {
				display: none;
			}
			@media (min-width: 992px) {
				font-size: 36px;
				line-height: 49px;
			}
		}
	}

	.btn.btn-primary {
		width: 166px;
		margin-top: 44px;
		margin-left: auto;
		@media (min-width: 992px) {
			width: 174px;
		}
	}
}

.form-control {
	background: #ffffff;
	border: 1px solid #c1c1c1;
	border-radius: 2px;
	height: 42px;
}
.form-label {
	font-weight: 400;
	font-size: 16px;
	line-height: 22px;
	/* identical to box height */

	letter-spacing: 0.05em;
	text-transform: lowercase;

	color: #000000;
	margin-bottom: 5px;
}

.remind {
	&__title {
		font-weight: 300;
		font-size: 20px;
		line-height: 27px;
		letter-spacing: 0.12em;
		text-transform: uppercase;
		color: $gold;
		margin-bottom: 10px;
		@media (min-width: 992px) {
			font-size: 36px;
			line-height: 49px;
		}

	}
	&__text {
		font-weight: 400;
		font-size: 10px;
		line-height: 14px;
		letter-spacing: 0.05em;
		text-transform: lowercase;

		/* Н3 */

		color: #737780;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
		}
	}
}
