.lk-menu {
	&__title {
		font-style: normal;
		font-weight: 600;
		font-size: 16px;
		line-height: 22px;
		/* identical to box height */

		text-transform: uppercase;

		color: #000000;
		margin-bottom: 20px;
	}
	ul{
		margin-bottom: 40px;
		list-style-type: none;
		padding-left: 0;
		li{
			&:not(:last-child){
				margin-bottom: 10px;
			}
			a{
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
				/* identical to box height */

				letter-spacing: 0.05em;

				/* Н3 */

				color: #737780;
				&.current{
					color: #000000;

				}
			}
		}
	}
}
