.breacrumbs{
	padding-top: 10px;
	margin-bottom: 40px;
	list-style-type: none;
	padding-left: 0;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
	justify-content: flex-start;
	
	.home{
		.svg{
			background-size: cover;
			background-repeat: no-repeat;
			width: 16px;
			height: 16px;
			margin-right: 5px;
		}
	}
	li{
		position: relative;
		&:not(:last-child){
			padding-right: 15px;
			&::after{
				content: '>';
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
			}
			
		}
		&:not(:first-child){
			padding-left: 15px;
		}
		a{
			font-style: normal;
			font-weight: 400;
			font-size: 10px;
			line-height: 14px;
			letter-spacing: 0.05em;
			display: flex;
			align-items: center;
			/* Н3 */

			color: #737780;
			@media (min-width:992px) {
				font-size: 12px;
				line-height: 16px;
			}
		}
	}

}