.product-page {
	margin-bottom: 44px;
	@media (min-width: 992px) {
		margin-bottom: 80px;
	}
}
.page-title {
	margin-bottom: 40px;
	.h1 {
		color: var(--Grey, #3f3f3f);
		font-family: $montserat;
		font-size: 20px;
		font-style: normal;
		font-weight: 600;
		line-height: normal;
		letter-spacing: 2.4px;
		text-transform: uppercase;

		@media (min-width: 992px) {
			font-size: 28px;
			font-weight: 600;
		}
	}
	.sticker {
		display: flex;
		align-items: center;
		justify-content: center;
		font-style: normal;
		font-weight: 600;
		font-size: 10px;
		line-height: 14px;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: lowercase;
		color: #f8f8f8;
		border-radius: 50px;
		padding: 5px 15px;
		margin-left: 10px;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
			margin-left: 15px;
		}
	}
}
.product-info {
	margin-bottom: 40px;
}
.info {
	&__gallery {
		width: 100%;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			width: 50%;
			margin-bottom: 0;
		}
		.slider-image {
			width: 100%;
			height: 100%;
			img {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
	&__chars {
		@media (min-width: 992px) {
			padding-left: 32px;
		}
		.chars {
			&__prices {
				margin-bottom: 40px;
				@media (min-width: 992px) {
					margin-bottom: 61px;
				}
				.prices {
					&__item {
						@media (min-width: 992px) {
							&:not(:last-child) {
								margin-right: 24px;
							}
						}
					}
					&__caption {
						font-style: normal;
						font-weight: 400;
						font-size: 12px;
						line-height: 16px;
						letter-spacing: 0.05em;
						text-transform: lowercase;

						color: #000000;
						margin-bottom: 5px;
					}
					&__value {
						font-style: normal;
						font-weight: 600;
						font-size: 16px;
						line-height: 22px;
						/* identical to box height */
						margin-bottom: 13px;
						letter-spacing: 0.05em;
						text-transform: lowercase;

						color: #000000;
					}
					&__from {
						font-style: normal;
						font-weight: 400;
						font-size: 12px;
						line-height: 16px;
						letter-spacing: 0.05em;
						text-transform: lowercase;

						color: #000000;
					}
				}
			}
		}
		.characts {
			&__title {
				font-style: normal;
				font-weight: 600;
				font-size: 16px;
				line-height: 22px;

				letter-spacing: 0.05em;

				color: #000000;
				margin-bottom: 20px;
			}
			ul {
				padding-left: 0;
				list-style-type: none;
				li {
					font-style: normal;
					font-weight: 600;
					font-size: 16px;
					line-height: 22px;
					/* identical to box height */

					letter-spacing: 0.05em;
					text-transform: capitalize;

					color: #000000;
					&:not(:last-child) {
						margin-bottom: 10px;
					}
					span {
						font-weight: 400;
					}
				}
			}
		}
	}
}
.f-carousel {
	&__slide {
		height: 290px;
		@media (min-width: 992px) {
			height: 410px;
		}
		img {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
.f-thumbs {
	&__slide {
		width: 90px;
		height: 90px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
.sizes {
	&__title {
		font-style: normal;
		font-weight: 600;
		font-size: 14px;
		line-height: 19px;
		/* identical to box height */

		letter-spacing: 0.05em;
		text-transform: capitalize;

		color: #000000;
		margin-bottom: 20px;

		@media (min-width: 992px) {
			font-size: 12px;
			line-height: 16px;
			margin-bottom: 28px;
		}
	}
	&__value {
		font-style: normal;
		font-weight: 400;
		font-size: 5px;
		line-height: 7px;
		/* identical to box height */
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		letter-spacing: 0.05em;
		text-transform: lowercase;

		color: #000000;
		padding: 5px;
		border: 1px solid #c1c1c1;
		border-radius: 2px;
		@media (min-width: 992px) {
			font-size: 12px;
			line-height: 16px;
			padding: 12px;
			border-radius: 5px;
		}
	}
	&__table {
		.table {
			max-width: 100%;
			th {
				font-style: normal;
				font-weight: 400;
				font-size: 10px;
				line-height: 14px;
				/* identical to box height */

				letter-spacing: 0.05em;
				text-transform: lowercase;
				text-align: center;
				color: #000000;
				@media (min-width: 992px) {
					font-size: 12px;
					line-height: 16px;
				}
			}
			td {
				vertical-align: middle;
				text-align: center;
			}
		}
	}
	&__price {
		font-style: normal;
		font-weight: 400;
		font-size: 10px;
		line-height: 14px;
		/* identical to box height */

		text-align: center;
		letter-spacing: 0.05em;
		color: #000000;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
		}
	}
	&__input {
		display: flex;
		align-items: center;
		justify-content: space-around;
		background: #ffffff;
		border-radius: 3px;
		filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.25));
		width: 77px;
		height: 22px;
		input,
		input:focus-visible,
		input:focus {
			width: 24px;
			font-style: normal;
			font-weight: 400;
			font-size: 9px;
			line-height: 13px;
			/* identical to box height */
			border: none;
			text-align: center;
			outline: none;
			height: 100%;
			@media (min-width: 992px) {
				font-size: 16px;
				line-height: 22px;
			}
		}
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			/* display: none; <- Crashes Chrome on hover */
			-webkit-appearance: none;
			margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
		}
		span {
			font-size: 16px;
			line-height: 22px;
			color: #d9d9d9;
			@media (min-width: 992px) {
				font-size: 24px;
			}
		}
		@media (min-width: 992px) {
			width: 134px;
			height: 40px;
		}
	}
}

.p-cart {
	margin-top: 40px;
	margin-bottom: 44px;
	@media (min-width: 992px) {
		margin-top: 0;
	}
	&__info {
		margin-bottom: 21px;
		@media (min-width: 992px) {
			margin-bottom: 26px;
		}
		.info {
			&__title {
				font-weight: 600;
				font-size: 14px;
				line-height: 19px;
				letter-spacing: 0.05em;
				color: #000000;
				margin-right: 59px;
				@media (min-width: 992px) {
					font-size: 12px;
					line-height: 16px;
					margin-bottom: 5px;
					margin-right: 0;
				}
			}
			&__price {
				font-weight: 600;
				font-size: 16px;
				line-height: 22px;
				letter-spacing: 0.05em;
				color: #000000;
				@media (min-width: 992px) {
					font-size: 16px;
					line-height: 22px;
				}
			}
		}
	}
	&__buttons {
		display: flex;
		justify-content: space-between;

		.btn {
			height: 44px;
			display: flex;
			align-items: center;
			justify-content: center;

			@media (min-width: 992px) {
				height: 50px;
			}
			&.in-cart {
				width: 166px;
			}
			&.wish {
				width: 48px;
				.svg {
					display: block;
					width: 18px;
					height: 16px;
					background-size: cover;
					background-position: center;
					background-repeat: no-repeat;
				}
			}
		}
		@media (min-width: 992px) {
			justify-content: flex-start;
			.btn {
				&:first-child {
					margin-right: 21px;
				}
			}
		}
	}
	@media (min-width: 992px) {
		position: sticky;
		top: 15px;
		margin-bottom: 15px;
	}
}
.product-description {
	&__title {
		font-style: normal;
		font-weight: 600;
		font-size: 14px;
		line-height: 19px;
		/* identical to box height */

		letter-spacing: 0.05em;
		text-transform: capitalize;

		color: #000000;
		margin-bottom: 10px;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
		}
	}
	&__text {
		font-style: normal;
		font-weight: 400;
		font-size: 12px;
		line-height: 16px;
		letter-spacing: 0.05em;
		margin-bottom: 21px;
		color: #737780;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
		}
	}
}
.slider-products {
	margin-bottom: 60px;
	@media (min-width: 992px) {
		margin-bottom: 80px;
	}
}
.slider-title {
	color: $grey;
	font-family: $montserat;
	font-size: 20px;
	font-style: normal;
	font-weight: 600;
	line-height: normal;
	letter-spacing: 2.4px;
	text-transform: uppercase;
	margin-bottom: 10px;
	@media (min-width: 992px) {
		color: #34353A;
		font-size: 28px;
		letter-spacing: 3.36px;
		margin-bottom: 20px;
	}
}
.slick-list {
	height: 100%;
	z-index: 5;
}
.slick-arrow {
	display: block;
	width: 25px;
	height: 25px;
	background: #fff;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 6;
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
	cursor: pointer;
	@media (min-width: 992px) {
		width: 50px;
		height: 50px;
		top: 50%;
	}
	&.next {
		right: 0;
		transform: translateY(-50%) translateX(25%);
	}
	&.prev {
		left: 0;
		transform: translateY(-50%) translateX(-25%);
	}
	.svg {
		display: block;
		width: 4px;
		height: 8px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		@media (min-width: 992px) {
			width: 6px;
			height: 12px;
		}
	}
	&.slick-disabled {
		opacity: 0.5;
	}
}
.slick-slide {
	margin: 0 10px;
}
.slider-item img {
	max-width: 100%;
}
