.success {
	padding-top: 15px;
	padding-bottom: 15px;
	margin-bottom: 60px;
	width: 100%;
	max-width: 584px;
	margin-left: auto;
	margin-right: auto;
	@media (min-width: 992px) {
		padding-top: 35px;
		padding-bottom: 25px;
		margin-bottom: 80px;
	}
	&__title {
		font-family: $montserat;
		font-size: 20px;
		font-style: normal;
		font-weight: 600;
		line-height: normal;
		letter-spacing: 2.4px;
		text-transform: uppercase;
		text-align: left;
		/* Н3 */
		margin-bottom: 20px;
		color: $gold;
		@media (min-width: 992px) {
			font-size: 28px;
			font-style: normal;
		}
	}

	&__text {
		color: var(--Grey, #3f3f3f);
		font-family: $montserat;
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		letter-spacing: 0.7px;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			font-size: 16px;
			letter-spacing: 0.8px;
		}
	}
	&__number {
		color: var(--Grey, #3F3F3F);
		font-family: $montserat;
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		letter-spacing: 0.7px;
		margin-bottom: 40px;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
			margin-bottom: 52px;
			letter-spacing: 0.8px;
		}
	}
	.btn {
		width: 252px;
		margin-bottom: 20px;
		margin-left: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		@media (min-width: 992px) {
			width: 259px;
		}
	}

}
