.reviews {
	width: 100%;

	@media (min-width: 992px) {
	}
	&__item {
		padding-top: 27px;
		padding-bottom: 23px;
		padding-right: 20px;
		padding-left: 20px;
		background: #ffffff;
		
		margin-bottom: 10px;
		box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
		@media (min-width: 992px) {
			padding: 0;
			margin-bottom: 20px;
			box-shadow: none;
		}
	}
	.item{
		&__image{
			width: 78px;
			height: 78px;
			margin-bottom: 10px;
			display: block;
			@media (min-width: 992px) {
				width: 173px;
				min-width: 173px;
				height: 192px;
				margin-bottom: 0;
				margin-right: 35px;
			}
			img{
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__name{
			font-weight: 600;
			font-size: 12px;
			line-height: 16px;
			letter-spacing: 0.05em;
			text-transform: capitalize;

			color: #000000;
			margin-bottom: 10px;
			@media (min-width: 992px) {
				font-size: 16px;
				line-height: 22px;
			}
		}
		&__comment{
			font-weight: 400;
			font-size: 10px;
			line-height: 14px;

			color: #000000;
			@media (min-width: 992px) {
				font-size: 15px;
				line-height: 20px;
			}
		}
		&__text{
			@media (min-width: 992px) {
				background: #FFFFFF;
				box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
				padding: 20px;
			}
		}
	}
}
