.wedo{
	padding-top: 40px;
	padding-bottom: 20px;
	@media (min-width:992px) {
		padding-top: 80px;
		padding-bottom: 25px;
	}
	&__item{
		height: 216px;
		overflow: hidden;
		position: relative;
		@media (min-width:992px) {
			height: 242px;
		
		}
		img{
			object-fit: cover;
		}
		a{
			position: absolute;
			bottom: 0;
			width: 100%;
			padding-top: 5px;
			padding-bottom: 5px;
			background: rgba(184, 154, 138, 0.60);
			backdrop-filter: blur(2px);
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
			line-height: 16px;
			text-align: center;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: #fff;
			flex: none;
			order: 0;
			flex-grow: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			@media (min-width:992px) {
				height: 100%;
				max-height: 50px;
				font-size: 16px;
				line-height: 22px;
				transition: max-height 0.3s;
			}
		}
		
		.svg{
			width: 20px;
			height: 20px;
			position: absolute;
			right: 50%;
			transform: translateX(50%);
			bottom: 79px;
			display: none;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.3s;
			@media (min-width:992px) {
				display: block;
			}
		}
		&:hover{
			@media (min-width:992px) {
				a{
					max-height: 100%;
				}
				.svg{
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
}
.section-title{
	font-style: normal;
	font-size: 20px;
	line-height: 27px;
	letter-spacing: 0.12em;
	text-transform: uppercase;
	color: #34353A;
	margin-bottom: 20px;
	font-weight: 600;
	font-family: $montserat;
	@media (min-width:992px) {
		letter-spacing: 0.12em;
		font-size: 28px;
		margin-bottom: 24px;
	}
	a{

		right: 0;
		top: 50%;
		transform: translateY(-50%);
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		letter-spacing: 0.05em;
		text-transform: lowercase;
		color: #34353A;
		
		.svg{
			width: 20px;
			height: 20px;
			margin-right: 10px;
		}
	}
}