.weuse{
	padding-top: 20px;
	padding-bottom: 20px;
	@media (min-width:992px) {
		padding-top: 30px;
		padding-bottom: 50px;
	}
	&__item{
		overflow: hidden;
		display: block;
		span{
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
			line-height: 16px;
			text-align: center;
			letter-spacing: 0.05em;
			text-transform: lowercase;
			color: #FFFFFF;
			display: flex;
			align-items: center;
			justify-content: center;
			padding-top: 5px;
			padding-bottom: 5px;
			background: rgba(52, 53, 58, 0.5);
			width: 100%;
			bottom: 0;
			@media (min-width:992px) {
				left: 0;
				top: 20px;
				width: auto;
				height: 40px;
				padding-right: 20px;
				padding-left: 20px;
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
			}
		}
		img{
			object-fit: cover;
		}
	}
}