.text-page {
	margin-bottom: 60px;
	@media (min-width: 992px) {
		margin-bottom: 80px;
	}
	p {
		font-weight: 400;
		font-size: 10px;
		line-height: 14px;
		letter-spacing: 0.05em;
		margin-bottom: 10px;
		/* Н3 */

		color: #737780;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
			margin-bottom: 15px;
		}
	}
	ul {
		font-size: 10px;
		line-height: 14px;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
		}
	}
	&__text-bg {
		height: 340px;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		padding-top: 29px;
		padding-left: 19px;
		padding-bottom: 32px;
		margin-bottom: 20px;
		@media (min-width: 992px) {
			padding-top: 62px;
			height: 400px;
			padding-left: 100px;
			padding-bottom: 61px;
			margin-bottom: 33px;
		}
		.title {
			font-weight: 400;
			font-size: 20px;
			line-height: 23px;
			letter-spacing: 0.12em;
			text-transform: uppercase;
			text-align: left;
			margin-bottom: 20px;
			width: 80%;
			@media (min-width: 992px) {
				font-size: 36px;
				line-height: 42px;
				width: 60%;
				margin-bottom: 40px;
			}
		}
		.text {
			width: 80%;
			font-weight: 400;
			font-size: 10px;
			line-height: 14px;
			letter-spacing: 0.05em;
			width: 60%;
			@media (min-width: 992px) {
				font-size: 16px;
				line-height: 22px;
			}
			ul {
				padding-left: 10px;
			}
		}
		&.txt-right {
			padding-top: 31px;
			padding-right: 14px;
			padding-bottom: 30px;
			height: 528px;
			@media (min-width: 992px) {
				padding-top: 101px;
				padding-bottom: 102px;
				height: 600px;
			}
			.title {
				text-align: left;
				width: 250px;
				margin-left: auto;
				@media (min-width: 992px) {
					width: 687px;
				}
			}
			.text {
				text-align: left;
				width: 195px;
				margin-left: auto;
				@media (min-width: 992px) {
					width: 687px;
				}
			}
		}
	}
}
.diplom-gallery {
	img {
		width: 45%;
		margin-right: 5px;
		margin-left: 5px;
		margin: 5px;
		margin-top: 5px;
		@media (min-width: 992px) {
			width: 23%;
		}
	}
}

.contacts {
	&__1{
		margin-bottom: 60px;
	}
	a{
		color: var(--Black, #060606);
		text-align: right;
		font-family: $montserat;
		font-size: 14px;
		font-style: normal;
		font-weight: 500;
		line-height: normal;	
		
		&.tel{
			margin-bottom: 20px;
		}
		&.mail{
			margin-bottom: 30px;
		@media (min-width: 992px) {
			margin-bottom: 48px;
		}

		}
	}
	p {
		margin-bottom: 20px;
		font-weight: 400;
		font-size: 10px;
		line-height: 14px;
		letter-spacing: 0.05em;

		color: #000000;
		@media (min-width: 992px) {
			font-size: 16px;
			line-height: 22px;
			margin-bottom: 40px;
		}
	}
	&__image {
		margin-bottom: 36px;
		@media (min-width: 992px) {
			margin-bottom: 0;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.reqs {
		&__title {
			font-weight: 600;
			font-size: 10px;
			line-height: 14px;
			/* identical to box height */
			margin-bottom: 20px;
			letter-spacing: 0.05em;
			@media (min-width: 992px) {
				margin-bottom: 40px;

				font-size: 16px;
				line-height: 22px;
			}
		}
		&__list {
			li {
				margin-bottom: 20px;
				font-weight: 400;
				font-size: 10px;
				line-height: 14px;
				letter-spacing: 0.05em;
				@media (min-width: 992px) {
					font-size: 16px;
					line-height: 22px;
				}
			}
		}
	}
}
.ya-map {
	display: block;
	height: 241px;
	@media (min-width: 992px) {
		height: 353px;
	}
	iframe {
		width: 100%;
		height: 100%;
	}
}
.oform {
	&__item {
		height: 100%;
		padding: 19px 7px;
		margin-bottom: 11px;
		background: #ffffff;
		box-shadow: 0px 0px 3.02198px rgba(0, 0, 0, 0.25);

		.svg {
			width: 24px;
			height: 24px;
			background-position: center;
			background-size: cover;
			background-repeat: no-repeat;
			margin-bottom: 5px;
			margin-bottom: 6px;
			@media (min-width: 992px) {
				width: 40px;
				height: 40px;
			}
		}
		.title {
			font-size: 8px;
			line-height: 11px;
			text-align: center;
			letter-spacing: 0.05em;

			color: #000000;
			@media (min-width: 992px) {
				font-size: 16px;
				line-height: 22px;
			}
		}
	}
}

.article {
	&__name {
		font-weight: 600;
		font-size: 16px;
		line-height: 22px;
		letter-spacing: 0.05em;
		color: #34353a;
		margin-bottom: 10px;
		@media (min-width: 992px) {
			margin-bottom: 24px;
			font-size: 16px;
			line-height: 22px;
		}
		&__text {
			p{
				font-weight: 400;
				font-size: 16px;
				line-height: 22px;
				letter-spacing: 0.05em;
	
				/* Н3 */
	
				color: #737780;
				@media (min-width: 992px) {
					font-size: 16px;
					line-height: 22px;
				}
			}
		
			img{
				margin-top: 20px;
				@media (min-width: 992px) {
					margin-top: 24px;
				}
			}
		}
	}

}

.text-delievery{
	
	ul{
		margin-top: 2rem;
		margin-bottom: 4rem;
		li{
			margin-bottom: 1rem;
		}
	}
	
}